@import url(https://fonts.googleapis.com/css2?family=Style+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Asap:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search__default-container {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 24px;
}

.search__default-container li {
  text-decoration: none;
  color: #4aadc9;
  background-color: #4f586b;
  padding: 5px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.search__default-container li:hover {
  cursor: pointer;
  color: #4aadc9;
  background-color: #363d4b;
}
.search__form {
  display: flex;
}

input[type="text"] {
  color: #282c34;
  outline: none;
  padding: 7px;
  border: none;
  width: 100%;
  border-radius: 10px;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.search-submit {
  color: #56c4e2;
  background-color: #4f586b;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 7px;
  outline: none;
  border: none;
  letter-spacing: 1px;
  border-top: 2px solid;
  border-bottom: 2px solid;
  transition: all 0.3s ease;
}

.search-submit:hover {
  background-color: #636d85;
}
.app-content {
  width: 1200px;
  margin: 75px auto;
}

.image__card-wrap {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
}

.image__card img {
  max-width: 100%;
  max-height: auto;
  position: relative;
  vertical-align: middle;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  height: 290px;
  width: 290px;
  object-fit: cover;
}
* {
  box-sizing: border-box;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  font-family: 'Asap', sans-serif;
}

h2, h3, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  object-fit: cover;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #4aadc9;
  background-color: #4f586b;
  padding: 5px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

a:hover {
  color: #4aadc9;
  background-color: #363d4b;
}

.heading {
  font-family: 'Style Script', cursive;
  letter-spacing: 5px;
  color: #56c4e2;
  text-align: center;
}

.search-wrapper {
  max-width: 700px;
  width: 700px;
  margin: 0 auto;
}
